.content {
    margin-top: 20vh;
    margin-bottom: 10vh;
    display: flex;
    justify-self: center;
    flex-direction: column;
    align-items: center;
    font-size: calc(10px + 2vmin);
    width: 700px;
    animation: fadeIn 0.3s ease-in;

    h1 {
        margin-bottom:  10vh;
    }

    p {
        text-align: center;
        font-size: 12px;
    }

    ul {
        width: 300px;
        height: 150px;

        list-style-type: square;
    }

    li {
        color: var(--text-colour);
        font-size: 14px;
        }

    li:nth-child(1) {
        animation-delay: 0s;
    }
    li:nth-child(2) {
        animation-delay: 0.2s;
        /* 0.2s delay for the second item */
    }
    li:nth-child(3) {
        animation-delay: 0.4s;
        /* 0.4s delay for the third item */
    }
    li:nth-child(4) {
    animation-delay: 0.6s; /* 0.6s delay for the fourth item */
    }
    li:nth-child(5) {
    animation-delay: 0.8s; /* 0.8s delay for the fifth item */
    }
}

.container {
    display: flex;
}

.divider {
    width: 700px;
    height: 2px;
    background-color: var(--brown-highlight);
    margin: 40px auto;

    display: flex;

    /* Add animation */
}

.staggeredList li {
  opacity: 0; /* Start hidden */
  animation: staggerFadeIn 0.5s forwards; /* Apply the animation */
}

@media (width < 800px) {
    .content {
        width: 300px;
        margin-top: 50px;
    }
    .divider {
        width: 90%;
    }
    .container {
        flex-direction: column;
    }
}

@keyframes staggerFadeIn { /* used for the li element */
  from {
    opacity: 0;
    transform: translateY(20px); /* Optional: add a slight upward movement */
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes fadeIn { /* used for the page display */
    0% {
        transform: translateY(20px), scale(0.9);
        opacity: 0;
    }

    100% {
        transform: translateY(0px), scale(1);
        opacity: 1;
    }
}